@import "color";

footer{
    background-color: $first;
    height: 200px;
    width: 100%;
    color: $dark;
    display: grid;
    grid-template-columns: 4fr 1fr;
    padding: 2rem;

    > div{
        display: flex;
        flex-direction: column;
        justify-content: center;
        > h1{
            font: 700 3rem $text1;
            text-transform: uppercase;
            color: $third;
           }
        > p{
            font: 500 1.2rem $text1;
          }
        > h5{
            font: 300 2rem $text1;
            margin: 1rem 0;
            text-align: center;
        } 
    span{
            display: flex;
            flex-direction: column;
            align-items: center;
             &:hover{
                 color: rosybrown;
                 font: 900 1.5rem $text2;
             }
        }
        
    }
}
